import React from "react"
import Stars from "../images/stars.jpg"
import { Input } from "./input";

interface IColorLockViewProps {
    onBack: () => void;
    onDone: () => void;
}

export function ColorLockView(props: IColorLockViewProps): JSX.Element {
    const validateInput = (key: string) => {
        if (key.length != 1 || !key.match(/[wbcory]/i)) {
            return undefined;
        }

        return key.toUpperCase();
    }

    return (
        <div className="lock">
            <div className="txt">
                <h1>Gathering supplies</h1>
                <p>You will need some food supplies on your trip back to Earth, but the freezer is locked. You see the following password hint scribbled on a Post-it:</p>
                <p style={{ textAlign: "center" }}><em>Star colors from hottest to coolest</em></p>
                <p>This and the picture underneath it should be enough for you to figure out the password...</p>
            </div>
            <div>
                <img src={Stars} style={{ margin: "auto", display: "block", maxWidth: "600px" }}></img>
            </div>
            <div className="legend" style={{ textAlign: "center" }}>
                <p><span style={{ backgroundColor: "#ffffff" }}><strong>W</strong>hite</span> <span style={{ backgroundColor: "#ffc000" }}><strong>O</strong>range</span> <span style={{ backgroundColor: "#45c6d9" }}><strong>C</strong>yan</span> <span style={{ backgroundColor: "#ffff01"}}><strong>Y</strong>ellow</span> <span style={{ backgroundColor: "#c00000" }}><strong>R</strong>ed</span> <span style={{ backgroundColor: "#01b0f1" }}><strong>B</strong>lue</span></p>
            </div>
            <div>
                <Input expected="BCWYOR" onDone={props.onDone} validateInput={validateInput}></Input>
            </div>
            <div className="txt">
                <p>... or you could always <a href="#" onClick={props.onBack}>go back</a> and try again later.</p>
            </div>
        </div>);
}
